"use client";

import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  CartesianGrid,
} from "recharts";
import { TrendingUp } from "lucide-react";
import { cn } from "@/lib/utils";
import { TrendDataPoint } from "@/types";
import { useState, useEffect } from "react";
import { fetchFearGreedHistory, transformHistoryData, calculateChange } from "@/lib/api";

interface SentimentAreaChartProps {
  data: TrendDataPoint[];
  currentScore: number;
  change24h: number;
}

type TimePeriod = "Week" | "Month" | "Year";

const periodToDays: Record<TimePeriod, number> = {
  Week: 7,
  Month: 30,
  Year: 365,
};

export function SentimentAreaChart({
  data: initialData,
  currentScore: initialScore,
  change24h: initialChange,
}: SentimentAreaChartProps) {
  const [selectedPeriod, setSelectedPeriod] = useState<TimePeriod>("Week");
  const [data, setData] = useState<TrendDataPoint[]>(initialData);
  const [currentScore, setCurrentScore] = useState(initialScore);
  const [change, setChange] = useState(initialChange);
  const [isLoading, setIsLoading] = useState(false);

  const isPositive = change >= 0;

  useEffect(() => {
    const fetchData = async () => {
      setIsLoading(true);
      try {
        const days = periodToDays[selectedPeriod];
        const response = await fetchFearGreedHistory(days);
        const transformedData = transformHistoryData(response);
        const scores = response.data.datasets[0].data;

        setData(transformedData);
        setCurrentScore(scores[scores.length - 1]);
        setChange(calculateChange(scores));
      } catch (error) {
        console.error("Failed to fetch data:", error);
      } finally {
        setIsLoading(false);
      }
    };

    fetchData();
  }, [selectedPeriod]);

  return (
    <Card className="card-crypto border-0 h-full flex flex-col">
      <CardHeader className="flex flex-row items-center justify-between pb-2">
        <div>
          <CardTitle className="text-lg text-muted-foreground font-medium mb-2">
            Market Sentiment Trend
          </CardTitle>
          <div className="flex items-center gap-3">
            <span className="data-value">{currentScore.toFixed(0)}</span>
            <Badge
              variant={isPositive ? "success" : "destructive"}
              className="flex items-center gap-1"
            >
              <TrendingUp
                className={cn("w-3 h-3", !isPositive && "rotate-180")}
              />
              {isPositive ? "+" : ""}
              {change.toFixed(2)}%
            </Badge>
          </div>
        </div>

        {/* Time Range Selector */}
        <div className="flex gap-2">
          {(["Week", "Month", "Year"] as TimePeriod[]).map((period) => (
            <button
              key={period}
              onClick={() => setSelectedPeriod(period)}
              disabled={isLoading}
              className={cn(
                "px-4 py-2 rounded-button text-sm font-medium transition-all",
                selectedPeriod === period
                  ? "bg-primary text-white"
                  : "bg-background-tertiary text-muted-foreground hover:text-foreground",
                isLoading && "opacity-50 cursor-not-allowed"
              )}
            >
              {period}
            </button>
          ))}
        </div>
      </CardHeader>

      <CardContent className="pt-6 flex-1 flex items-center">
        <ResponsiveContainer width="100%" height={300}>
          <AreaChart data={data}>
            <defs>
              <linearGradient id="colorScore" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor="#FF6B4A" stopOpacity={0.8} />
                <stop offset="95%" stopColor="#8B4513" stopOpacity={0.1} />
              </linearGradient>
            </defs>
            <CartesianGrid strokeDasharray="3 3" stroke="#333" />
            <XAxis dataKey="time" stroke="#666" style={{ fontSize: "12px" }} />
            <YAxis stroke="#666" style={{ fontSize: "12px" }} />
            <Tooltip
              contentStyle={{
                backgroundColor: "#242424",
                border: "1px solid #333",
                borderRadius: "8px",
                color: "#fff",
              }}
            />
            <Area
              type="monotone"
              dataKey="score"
              stroke="#FF6B4A"
              strokeWidth={2}
              fillOpacity={1}
              fill="url(#colorScore)"
            />
          </AreaChart>
        </ResponsiveContainer>
      </CardContent>
    </Card>
  );
}
